<template>
	<div>
		<div>
			<a-button type="primary" icon="plus-circle" @click="jump">
				添加
			</a-button>
			<div style="display: flex; justify-content: flex-start;">
				<div>
					搜关键字：
					<a-input style="width:250px; margin: 20px 0 ;" v-model="params.key" placeholder="用户名/管理员/管理员邮箱" />
				</div>
				<div style="margin: 20px 0 0 50px">
					公告时间区间：
					<a-range-picker @change="onChange" style="width:250px; " :locale="locale" />
				</div>
				<a-button type="primary" icon="search" style="margin: 20px 0 0 60px" @click="search">
					搜索
				</a-button>
			</div>
			<div v-show="!collapsed" style="display: flex; ">
				<div>
					用户职业：
					<a-select v-model="params.occupation" style="width: 200px;" placeholder="选择职业" :allowClear="true">
						<a-select-option :value=key v-for="(value, key) in occupationList">
							{{value}}
						</a-select-option>
					</a-select>
				</div>
				<div style="margin-left: 40px;">
					用户年龄段：
					<a-select v-model="params.age" style="width: 150px;" placeholder="选择年龄段" :allowClear="true">
						<a-select-option :value=key v-for="(value, key) in ageList">
							{{value}}
						</a-select-option>
					</a-select>
				</div>
				<div style="margin-left: 40px;">
					用户性别：
					<a-select v-model="params.gender" style="width: 150px;" placeholder="选择性别" :allowClear="true">
						<a-select-option value="M">男</a-select-option>
						<a-select-option value="F">女</a-select-option>
					</a-select>
				</div>
			</div>
			<a-divider style="color: #999999; margin-top: 5px;">
				<a-icon @click="collapsed=!collapsed" :type="collapsed?'down-circle':'up-circle'" />
				{{collapsed?"展开":"收起"}}更多选项
			</a-divider>


			<a-table :columns="columns" :data-source="dataSource" :loading="dataloading" :pagination=" {pageSize:pageSize}"
			 :scroll="{ y: 360 }">
				<span slot="userName" slot-scope="text, record">
					<a-popover placement="bottom">
						<template slot="content">
							<div>性别：{{record.userGender=="M"?"男":"女"}}</div>
							<div>年龄：{{ageList[record.userAge]}}</div>
							<div>职业：{{occupationList[record.userOccupation]}}</div>
						</template>
						<b>{{text}}</b>
					</a-popover>
				</span>
				<span slot="adminName" slot-scope="text, record">
					<b>{{text}}</b>
					<div style="text-decoration: underline;">{{record.adminEmail}}</div>
				</span>
				<span slot="content" slot-scope="text, record">
					<a-popover placement="bottom">
						<template slot="content">
							{{record.content}}
						</template>
						<div v-limit="[record.content,12]" v-text="record.content"></div>
					</a-popover>
				</span>
				<span slot="action" slot-scope="text, record">
					<div style="display: flex; justify-content: center;">
						<a-popconfirm title="是否确定删除？" ok-text="是" cancel-text="否" @confirm="confirm(record.id, record.uid)" @cancel="cancel">
							<a>删除</a>
						</a-popconfirm>
						<a-divider type="vertical" />
						<p style="cursor: pointer; color: #18aeff;" @click="showModifyWindow(record)"> 修改 </p>
					</div>
				</span>
			</a-table>
			<a-modal v-model="modify" title="修改公告 (所有接收方)" @ok="handleOk" :afterClose="cancel">
				<p>发出时间：{{modifyRecord.createdTime}}</p>
				<p>原公告：{{modifyRecord.content}}</p>
				<p>修改为：</p>
				<a-input type="textarea" v-model="newContent" />
			</a-modal>
		</div>
	</div>
</template>

<script>
	import locale from 'ant-design-vue/es/date-picker/locale/zh_CN';
	import {
		mapMutations,
		mapState
	} from "vuex";
	const columns = [{
			title: '接收用户',
			dataIndex: 'userName',
			scopedSlots: {
				customRender: 'userName'
			},
		},
		{
			title: '编辑',
			dataIndex: 'adminName',
			width: '300px',
			scopedSlots: {
				customRender: 'adminName'
			},
		},
		{
			title: '公告内容',
			dataIndex: 'content',
			scopedSlots: {
				customRender: 'content'
			},
		},
		{
			title: '创建时间',
			dataIndex: 'createdTime',
		},
		{
			title: '操作',
			dataIndex: 'action',
			scopedSlots: {
				customRender: 'action'
			},
		},
	];
	columns.forEach((r) => {
		r.align = 'center'
	});

	export default {
		data() {
			return {
				locale,
				params: {
					key: null,
					startTime: null,
					endTime: null,
					gender: null,
					age: null,
					occupation: null,
				},
				collapsed: true,
				dataSource: null,
				page: 1,
				pageSize: 10,
				dataloading: true,
				visible: false,
				modify: false,
				modifyRecord: {
					createdTime: null,
					modifyRecord: null,
				},
				newContent: null,
				columns,
				ageList: null,
				occupationList: null,
			};
		},
		computed: {
			...mapState(['requestPath', 'userInfo'])
		},
		async created() {
			this.getAllAge();
			this.getAllOccupation();
			this.getAllBulletin();
		},
		methods: {
			async getAllAge() {
				if (this.$store.state.userInfo) {
					let {
						data
					} = await this.$get(this.$store.state.requestPath + 'allAge');
					this.ageList = data.data;
				} else {
					console.log("userInfo not found");
				}
			},
			async getAllOccupation() {
				if (this.$store.state.userInfo) {
					let {
						data
					} = await this.$get(this.$store.state.requestPath + 'allOccupation');
					this.occupationList = data.data;
				} else {
					console.log("userInfo not found");
				}
			},
			async getAllBulletin() {
				this.dataloading = true;
				if (this.$store.state.userInfo) {
					let {
						data
					} = await this.$get(this.$store.state.requestPath + 'bulletin');
					this.dataSource = data.data;
					this.dataloading = false;
				} else {
					console.log("userInfo not found");
				}
			},
			async search() {
				this.dataloading = true;
				let {
					data
				} = await this.axios.post(this.requestPath + 'bulletin/select', this.params);
				this.dataSource = data.data;
				this.dataloading = false;
			},
			onChange(date, dateString) {
				this.params.startTime = dateString[0];
				this.params.endTime = dateString[1];
			},
			async confirm(bid, uid) {
				console.log('del:' + bid + ' ' + uid);
				let params = {
					bid: bid,
					uid: uid,
				}
				let {
					data
				} = await this.axios.post(this.requestPath + 'bulletin/delete', params);
				if (data.code == 0) {
					this.getAllBulletin();
					this.$message.success('删除成功');
				} else {
					this.$message.error('删除失败');
				}
			},
			async handleOk(e) {
				if (this.newContent != null && this.newContent != "") {
					this.modifyRecord.content = this.newContent;
					console.log(this.modifyRecord)
					let {
						data
					} = await this.axios.post(this.requestPath + 'bulletin/modify', this.modifyRecord);
					if (data.code == 0) {
						this.getAllBulletin();
						this.$message.success('修改成功');
					} else {
						this.$message.error('修改失败');
					}
				}
				this.newContent = null;
				this.modify = false;
			},
			cancel(e) {
				this.newContent = null;
				this.modify = false;
			},
			jump() {
				this.$router.push('/admin/add_bulletin');
			},
			updatePopWinState(state) {
				this.visible = state;
			},
			async updateDataSource() {
				this.getAllBulletin();
			},
			showModifyWindow(record) {
				this.modifyRecord = record;
				this.modify = true;
			},
		},
	};
</script>

<style lang="scss">
</style>
